<template>
  <div class="container">
    <h1>aggiungi soggetto produttore</h1>
    <hr />
    <div class="row">
      <div class="col-md-4">
        <div class="form-group">
          <label for="">cognome </label>
          <input
            class="form-control"
            type="text"
            v-model="surname"
            value="true"
          />
        </div>
      </div>
      <div class="col-md-4">
        <div class="form-group">
          <label for="">nome </label>
          <input class="form-control" type="text" v-model="name" value="true" />
        </div>
      </div>
      <div class="col-md-4">
        <div class="form-group">
          <label for="">cf </label>
          <input class="form-control" type="text" v-model="cf" value="true" />
        </div>
      </div>
      <div class="col-md-4">
        <div class="form-group">
          <label for="">IDAzienda (X17E9,123)</label>
          <input class="form-control" type="text" v-model="id" value="true" />
        </div>
      </div>
    </div>
    <div class="row">
      <div class="col-md-4">
        <div class="form-group">
          <button class="btn btn-success" @click="submit">Aggiungi</button>
        </div>
      </div>
    </div>
    <div class="row">
      <table class="table">
        <thead>
          <th>ID Azienda</th>
          <th>esito</th>
          <th>message</th>
        </thead>
        <tbody>
          <tr v-for="(item, index) in message" :key="index">
            <td>{{ item.ID }}</td>
            <td>{{ item.esito }}</td>
            <td>{{ item.message }}</td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      surname: "Lauritano",
      name: "Elena",
      cf: "LRTLNE84E57L219G",
      id: null,
      message: null,
    };
  },
  methods: {
    submit() {
      var data = { surname: this.surname, name: this.name, cf: this.cf };
      this.$store.dispatch("loading");
      this.axios
        .post(this.apiURL + "/retention/addProducer/ID/" + this.id, data)
        .then((r) => {
          this.message = r.data;
          this.$store.dispatch("loaded");
        })
        .catch(function (r) {
          console.log(r);
          console.log("FAILURE!!");
          this.$store.dispatch("loaded");
        });
    },
  },
};
</script>
